
import React, {Fragment, useState } from 'react';
import API_KEY from '../../keys' // Carpeta RAIZ
 
const Form = (props) => {
	

	const [ciudad, setCiudad ] = useState("");
	const [pais, setPais ] = useState("");

	const handleSubmit = (e) => {
		e.preventDefault();

		const API_URL = `http://api.openweathermap.org/data/2.5/weather?q=${ciudad},${pais}&APPID=${API_KEY}`;

		
		 
		fetch(API_URL)
		.then(function(response) {
			return response.json();
		})
		.then(function(myJson) {
			console.log(myJson);
			props.handleChangeResultado();
		})
		.catch(function(error) {
			alert('Hubo un problema con la petición Fetch:' + error.message);
			console.log('Hubo un problema con la petición Fetch:' + error.message);
		});
	}

  return (
		<div className="container">
			<form className="col s12" onSubmit={handleSubmit}>
				<div className="row">
					<div className="input-field col s6">
						<i className="material-icons prefix">web_asset</i>
						<input 
						id="ciudad" 
						name="ciudad" 
						type="text" 
						className="validate" 
						onChange={e => setCiudad(e.target.value)}
						/>
						<label htmlFor="ciudad">Ciudad</label>
					</div>
					<div className="input-field col s6">
						<i className="material-icons prefix">terrain</i>
						<input 
						id="pais" 
						name="pais" 
						type="text" 
						className="validate"
						onChange={e=>setPais(e.target.value)}
						/>
						<label htmlFor="pais">Pais</label>
					</div>
					<div className="input-field col s6">
						
						<button className="btn waves-effect waves-light btn-small" type="submit" name="action">Submit
						<i className="material-icons right">send</i>
						</button>
					</div>
				</div>
			</form>
		</div>
	);
  }

  export default Form;